class FavoritesManager {
  constructor() {
    this.articlesCloudObj = null;
    this.init();
  }
  
  init() {
    try {
      this.articlesCloudObj = uniCloud.importObject('articlesCloudObj');
      console.log('‚úÖ ‰∫ëÂØπË±°ÂàùÂßãÂåñÊàêÂäü');
    } catch (error) {
      console.error('‚ùå ÂàùÂßãÂåñ‰∫ëÂØπË±°Â§±Ë¥•:', error);
    }
  }
  
  // Ëé∑ÂèñÂΩìÂâçÁî®Êà∑ID - ‰øÆÂ§çÁâàÔºåÂÖºÂÆπ id Âíå _id
  getCurrentUserId() {
    try {
      // ÊñπÊ≥ï1Ôºö‰ªé uni-id-pages Â≠òÂÇ®Ëé∑Âèñ
      let userInfo = uni.getStorageSync('uni-id-pages-userInfo');
      console.log('üîç uni-id-pages Áî®Êà∑‰ø°ÊÅØ:', userInfo);
      
      if (userInfo) {
        // ‰ºòÂÖà‰ΩøÁî® _idÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàô‰ΩøÁî® id
        const userId = userInfo._id || userInfo.id;
        if (userId) {
          console.log('‚úÖ ÊâæÂà∞Áî®Êà∑ID:', userId);
          return userId;
        }
      }
      
      // ÊñπÊ≥ï2Ôºö‰ªé uni-id Ê†áÂáÜÂ≠òÂÇ®Ëé∑Âèñ
      userInfo = uni.getStorageSync('uni_id_userinfo');
      console.log('üîç uni-id Ê†áÂáÜÁî®Êà∑‰ø°ÊÅØ:', userInfo);
      
      if (userInfo) {
        const userId = userInfo._id || userInfo.uid || userInfo.id;
        if (userId) {
          console.log('‚úÖ ÊâæÂà∞Áî®Êà∑ID:', userId);
          return userId;
        }
      }
      
      console.warn('‚ùå Êú™ÊâæÂà∞ÊúâÊïàÁöÑÁî®Êà∑ID');
      return null;
      
    } catch (error) {
      console.error('Ëé∑ÂèñÁî®Êà∑IDÂ§±Ë¥•:', error);
      return null;
    }
  }
  
  // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
  async checkLogin() {
    const userId = this.getCurrentUserId();
    
    if (!userId) {
      uni.showToast({ title: 'ËØ∑ÂÖàÁôªÂΩï', icon: 'none' });
      return false;
    }
    
    return true;
  }

  // Ë∞ÉËØïÊñπÊ≥ï
  async debugFavorites() {
    try {
      if (!this.articlesCloudObj) this.init();
      const result = await this.articlesCloudObj.debugFavorites();
      console.log('üêõ Ë∞ÉËØïÁªìÊûú:', result);
      return result;
    } catch (error) {
      console.error('Ë∞ÉËØïÂ§±Ë¥•:', error);
    }
  }

  // Âà∑Êñ∞token
  async refreshTokenIfNeeded() {
    try {
      const uniIdCo = uniCloud.importObject('uni-id-co');
      const result = await uniIdCo.refreshToken();
      console.log('üîÑ tokenÂà∑Êñ∞ÁªìÊûú:', result);
      return result.errCode === 0;
    } catch (error) {
      console.error('Âà∑Êñ∞tokenÂ§±Ë¥•:', error);
      return false;
    }
  }
  
  // Ê∑ªÂä†Êî∂Ëóè
  async addFavorite(articleId) {
    try {
      const userId = this.getCurrentUserId();
      if (!userId) {
        uni.showToast({ title: 'ËØ∑ÂÖàÁôªÂΩï', icon: 'none' });
        return false;
      }
      
      // Âà∑Êñ∞token
      await this.refreshTokenIfNeeded();
      
      if (!this.articlesCloudObj) this.init();
      
      console.log('üì§ ÂèëÈÄÅÊî∂ËóèËØ∑Ê±Ç:', { articleId, userId });
      
      const result = await this.articlesCloudObj.addFavorite({
        articleId,
        userId
      });
      
      console.log('üì• Êî∂ËóèÂìçÂ∫î:', result);
      
      if (result.errCode === 0) {
        uni.showToast({ title: 'Êî∂ËóèÊàêÂäü', icon: 'success' });
        return true;
      } else {
        uni.showToast({ title: result.errMsg, icon: 'none' });
        return false;
      }
    } catch (error) {
      console.error('‚ùå Êî∂ËóèÂ§±Ë¥•:', error);
      uni.showToast({ title: 'Êî∂ËóèÂ§±Ë¥•', icon: 'none' });
      return false;
    }
  }
  
  // ÂèñÊ∂àÊî∂Ëóè
  async removeFavorite(articleId) {
    try {
      const userId = this.getCurrentUserId();
      if (!userId) {
        uni.showToast({ title: 'ËØ∑ÂÖàÁôªÂΩï', icon: 'none' });
        return false;
      }
      
      if (!this.articlesCloudObj) this.init();
      
      console.log('üì§ ÂèëÈÄÅÂèñÊ∂àÊî∂ËóèËØ∑Ê±Ç:', { articleId, userId });
      
      const result = await this.articlesCloudObj.removeFavorite({
        articleId,
        userId
      });
      
      console.log('üì• ÂèñÊ∂àÊî∂ËóèÂìçÂ∫î:', result);
      
      if (result.errCode === 0) {
        uni.showToast({ title: 'ÂèñÊ∂àÊî∂Ëóè', icon: 'success' });
        return true;
      } else {
        uni.showToast({ title: result.errMsg, icon: 'none' });
        return false;
      }
    } catch (error) {
      console.error('ÂèñÊ∂àÊî∂ËóèÂ§±Ë¥•:', error);
      uni.showToast({ title: 'ÂèñÊ∂àÊî∂ËóèÂ§±Ë¥•', icon: 'none' });
      return false;
    }
  }
  
  // Ê£ÄÊü•Êî∂ËóèÁä∂ÊÄÅ
  async checkFavorite(articleId) {
    try {
      const userId = this.getCurrentUserId();
      if (!userId) return false;
      
      if (!this.articlesCloudObj) this.init();
      
      console.log('üì§ Ê£ÄÊü•Êî∂ËóèÁä∂ÊÄÅ:', { articleId, userId });
      
      const result = await this.articlesCloudObj.checkFavorite({
        articleId,
        userId
      });
      
      console.log('üì• Êî∂ËóèÁä∂ÊÄÅÂìçÂ∫î:', result);
      
      return result.data?.isFavorited || false;
    } catch (error) {
      console.error('Ê£ÄÊü•Êî∂ËóèÁä∂ÊÄÅÂ§±Ë¥•:', error);
      return false;
    }
  }
  
  // Ëé∑ÂèñÊî∂ËóèÂàóË°® - ÊúÄÁªàÁâà
  async getFavoritesList(page = 1, size = 100) {
    try {
      const userId = this.getCurrentUserId();
      console.log('üë§ ÂΩìÂâçÁî®Êà∑ID:', userId);
      
      if (!userId) {
        console.log('Áî®Êà∑Êú™ÁôªÂΩïÔºåËøîÂõûÁ©∫Êï∞ÁªÑ');
        return [];
      }
      
      // Âà∑Êñ∞token
      await this.refreshTokenIfNeeded();
      
      // ÂÖàËøõË°åË∞ÉËØïÔºàÂèØÈÄâÔºâ
      // await this.debugFavorites();
      
      if (!this.articlesCloudObj) {
        this.init();
        if (!this.articlesCloudObj) {
          console.error('‚ùå ‰∫ëÂØπË±°ÂàùÂßãÂåñÂ§±Ë¥•');
          return [];
        }
      }
      
      console.log('üì§ ÂèëÈÄÅÊî∂ËóèÂàóË°®ËØ∑Ê±Ç:', { page, size, userId });
      
      const result = await this.articlesCloudObj.getFavoritesList({
        page,
        size,
        userId
      });
      
      console.log('üì• Êî∂ËóèÂàóË°®ÂìçÂ∫î:', result);
      
      if (result.errCode === 0) {
        console.log(`‚úÖ Ëé∑ÂèñÂà∞ ${result.data.length} Êù°Êî∂Ëóè`);
        return result.data || [];
      } else {
        console.warn('‚ùå Ëé∑ÂèñÊî∂ËóèÂàóË°®Â§±Ë¥•:', result.errMsg);
        // Âç≥‰ΩøÂ§±Ë¥•‰πüËøîÂõûÁ©∫Êï∞ÁªÑÔºå‰∏çÊòæÁ§∫ÈîôËØØ
        return [];
      }
    } catch (error) {
      console.error('‚ùå Ëé∑ÂèñÊî∂ËóèÂàóË°®ÂºÇÂ∏∏:', error);
      // ÂèëÁîüÂºÇÂ∏∏Êó∂ËøîÂõûÁ©∫Êï∞ÁªÑ
      return [];
    }
  }
  
  // ÊâπÈáèÊ£ÄÊü•Êî∂ËóèÁä∂ÊÄÅ - Â¢ûÂº∫ÈîôËØØÂ§ÑÁêÜ
  async batchCheckFavorites(articleIds) {
    try {
      const userId = this.getCurrentUserId();
      
      if (!articleIds || !Array.isArray(articleIds) || articleIds.length === 0) {
        console.log('ÊñáÁ´†IDÂàóË°®‰∏∫Á©∫ÔºåËøîÂõûÁ©∫ÂØπË±°');
        return {};
      }

      // Áî®Êà∑Êú™ÁôªÂΩïÊó∂ÔºåÊâÄÊúâÊñáÁ´†ÈÉΩÊú™Êî∂Ëóè
      if (!userId) {
        console.log('Áî®Êà∑Êú™ÁôªÂΩïÔºåÊâÄÊúâÊñáÁ´†ÈªòËÆ§Êú™Êî∂Ëóè');
        const emptyStatus = {};
        articleIds.forEach(id => {
          emptyStatus[id] = false;
        });
        return emptyStatus;
      }
      
      // Âà∑Êñ∞token
      await this.refreshTokenIfNeeded();
      
      if (!this.articlesCloudObj) this.init();
      
      console.log('üì§ ÂèëÈÄÅÊâπÈáèÊ£ÄÊü•ËØ∑Ê±Ç:', { 
        articleCount: articleIds.length, 
        userId 
      });
      
      const result = await this.articlesCloudObj.batchCheckFavorites({
        articleIds,
        userId
      });
      
      console.log('üì• ÊâπÈáèÊ£ÄÊü•ÂìçÂ∫î:', result);
      
      if (result.errCode === 0) {
        return result.data || {};
      } else {
        console.warn('ÊâπÈáèÊ£ÄÊü•ËøîÂõûÈîôËØØ:', result.errMsg);
        // ËøîÂõûÈªòËÆ§Êú™Êî∂ËóèÁä∂ÊÄÅ
        const defaultStatus = {};
        articleIds.forEach(id => {
          defaultStatus[id] = false;
        });
        return defaultStatus;
      }
    } catch (error) {
      console.error('‚ùå ÊâπÈáèÊ£ÄÊü•Êî∂ËóèÁä∂ÊÄÅÂ§±Ë¥•:', error);
      
      // Âá∫ÈîôÊó∂ËøîÂõûÈªòËÆ§Êú™Êî∂ËóèÁä∂ÊÄÅ
      const defaultStatus = {};
      articleIds.forEach(id => {
        defaultStatus[id] = false;
      });
      return defaultStatus;
    }
  }

  // Ëé∑ÂèñÊî∂ËóèÊï∞ÈáèÔºà‰ºòÂåñÁâàÔºâ
  async getFavoritesCount() {
    try {
      const favoritesList = await this.getFavoritesList(1, 1000); // Ëé∑ÂèñË∂≥Â§üÂ§öÁöÑÊï∞ÊçÆÊù•ËÆ°Êï∞
      return favoritesList.length;
    } catch (error) {
      console.error('Ëé∑ÂèñÊî∂ËóèÊï∞ÈáèÂ§±Ë¥•:', error);
      return 0;
    }
  }
}

// ÂàõÂª∫Âçï‰æãÂÆû‰æã
const favoritesManager = new FavoritesManager();
export default favoritesManager;